// store/index.js 
import Vue from 'vue';
import Vuex from 'vuex';
import DB from '../api/DB'
import ScenicSpotService from '../services/ScenicSpotService';
Vue.use(Vuex); //vue

//Vuex.Store 
const store = new Vuex.Store({
	state: { //
		scenicSpotList:[]
	},

	mutations: {
		setPcShow(state, flag) {
			state.isPcShow = flag;
		},
		setUser(state, user) {
			state.user = user;
		},
		// this.$store.commit('updateHotelList',[])
		updateScenicSpotList(state, list) {
			state.scenicSpotList = list;
		},
        async getScenicSpotList(state,hotel_id){
            console.log("getScenicSpotList");
            
            try {
                uni.showLoading();
              const res = await   ScenicSpotService.getScenicSpotList(hotel_id);
              console.log("",res)
              store.commit('updateScenicSpotList', res.result.data);
              uni.hideLoading();
            } catch (error) {
              console.error(error);
              uni.hideLoading();
                uni.showModal({
                  content: "",
                  confirmText: "",
                });
            }
          }
	}
})
export default store;